/* Pin Rhythm Game - Gaming UI Styles */

:root {
    /* Gaming Color Palette */
    --primary-purple: #8b5cf6;
    --primary-pink: #ec4899;
    --primary-blue: #3b82f6;
    --primary-cyan: #06b6d4;

    /* Dark Gaming Background */
    --bg-dark: #0f0f23;
    --bg-darker: #0a0a1e;
    --bg-surface: #1e1b4b;
    --bg-surface-light: #312e81;

    /* Neon Glows */
    --glow-purple: rgba(139, 92, 246, 0.5);
    --glow-pink: rgba(236, 72, 153, 0.5);
    --glow-blue: rgba(59, 130, 246, 0.5);
    --glow-cyan: rgba(6, 182, 212, 0.5);
}

* {
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
    background: linear-gradient(135deg, var(--bg-dark) 0%, var(--bg-darker) 50%, var(--bg-surface) 100%);
    /* background-attachment: fixed 제거 - 기본 프레임과 충돌 방지 */
    /* min-height: 100vh 제거 - 기본 프레임 공간 확보 */
    overflow-x: hidden;
}

/* Gaming Typography */
.font-gaming {
    font-family: 'Orbitron', monospace;
    font-weight: 700;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}

.text-glow-purple {
    text-shadow: 0 0 10px var(--glow-purple), 0 0 20px var(--glow-purple), 0 0 30px var(--glow-purple);
}

.text-glow-pink {
    text-shadow: 0 0 10px var(--glow-pink), 0 0 20px var(--glow-pink), 0 0 30px var(--glow-pink);
}

/* Neon Box Shadows */
.shadow-glow-purple {
    box-shadow: 0 0 20px var(--glow-purple), 0 0 40px var(--glow-purple), inset 0 0 20px rgba(139, 92, 246, 0.1);
}

.shadow-glow-pink {
    box-shadow: 0 0 20px var(--glow-pink), 0 0 40px var(--glow-pink), inset 0 0 20px rgba(236, 72, 153, 0.1);
}

/* Gaming Buttons */
.btn-gaming {
    position: relative;
    background: linear-gradient(135deg, var(--primary-purple) 0%, var(--primary-pink) 100%);
    border: 2px solid transparent;
    border-radius: 12px;
    padding: 12px 24px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    overflow: hidden;
}

.btn-gaming::before {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: linear-gradient(45deg, var(--primary-purple), var(--primary-pink), var(--primary-blue), var(--primary-cyan));
    border-radius: 14px;
    z-index: -1;
    animation: rotate-border 2s linear infinite;
}

.btn-gaming:hover {
    transform: translateY(-2px) scale(1.05);
    box-shadow: 0 0 30px var(--glow-purple), 0 10px 25px rgba(139, 92, 246, 0.3);
}

/* Gaming Cards */
.card-gaming {
    background: linear-gradient(135deg, rgba(30, 27, 75, 0.8) 0%, rgba(49, 46, 129, 0.6) 100%);
    border: 1px solid rgba(139, 92, 246, 0.3);
    border-radius: 16px;
    backdrop-filter: blur(20px);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.card-gaming:hover {
    transform: translateY(-4px) scale(1.02);
    border-color: rgba(139, 92, 246, 0.6);
    box-shadow: 0 0 30px rgba(139, 92, 246, 0.2), 0 20px 40px rgba(0, 0, 0, 0.3);
}

/* Pulse Animations */
@keyframes pulse-glow {

    0%,
    100% {
        box-shadow: 0 0 20px var(--glow-purple);
    }

    50% {
        box-shadow: 0 0 40px var(--glow-purple), 0 0 60px var(--glow-purple);
    }
}

@keyframes rotate-border {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.animate-pulse-glow {
    animation: pulse-glow 2s ease-in-out infinite;
}

/* Gaming Scrollbar */
::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: var(--bg-darker);
    border-radius: 4px;
}

::-webkit-scrollbar-thumb {
    background: linear-gradient(180deg, var(--primary-purple) 0%, var(--primary-pink) 100%);
    border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(180deg, var(--primary-pink) 0%, var(--primary-purple) 100%);
    box-shadow: 0 0 10px var(--glow-purple);
}

/* Selection Styling */
::-moz-selection {
    background: var(--primary-purple);
    color: white;
}
::selection {
    background: var(--primary-purple);
    color: white;
}

::-moz-selection {
    background: var(--primary-purple);
    color: white;
}

/* Focus Styles */
*:focus {
    outline: 2px solid var(--primary-purple);
    outline-offset: 2px;
}

/* Gaming Loading Animation */
.loading-dots {
    display: inline-block;
}

.loading-dots::after {
    content: '';
    animation: loading-dots 1.5s steps(4, end) infinite;
}

@keyframes loading-dots {

    0%,
    20% {
        content: '';
    }

    40% {
        content: '.';
    }

    60% {
        content: '..';
    }

    80%,
    100% {
        content: '...';
    }
}

/* Game UI Elements */
.game-ui-border {
    border: 2px solid;
    -o-border-image: linear-gradient(45deg, var(--primary-purple), var(--primary-pink), var(--primary-blue)) 1;
       border-image: linear-gradient(45deg, var(--primary-purple), var(--primary-pink), var(--primary-blue)) 1;
    border-radius: 8px;
}

.game-ui-bg {
    background: linear-gradient(135deg,
            rgba(30, 27, 75, 0.95) 0%,
            rgba(49, 46, 129, 0.9) 25%,
            rgba(30, 27, 75, 0.95) 50%,
            rgba(49, 46, 129, 0.9) 75%,
            rgba(30, 27, 75, 0.95) 100%);
    backdrop-filter: blur(20px);
    border: 1px solid rgba(139, 92, 246, 0.2);
}

